import svg4everybody from 'svg4everybody';
import $ from 'jquery';
import 'fullpage.js';
import '../../node_modules/wowjs/dist/wow.js';
import '../blocks/fullpage/fullpage.js';
import '../blocks/menu/menu.js';

wow = new WOW(
	{
	boxClass:     'wow',      // default
	animateClass: 'animated', // default
	offset:       0,          // default
	mobile:       true,       // default
	live:         true        // default
  }
  )
  wow.init();
$(() => {
	svg4everybody();
});



